<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar flat color="white">
        <v-toolbar-title>Tipos de anuncio</v-toolbar-title>

        <v-spacer></v-spacer>

        <!--Campo de bsqueda de tipos de anuncio-->
        <v-text-field
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Bsqueda"
          single-line
          hide-details=""
        ></v-text-field>
        <v-spacer></v-spacer>

        <v-btn medium color="indigo white--text" @click="crearNuevo"
          >Agregar tipo de anuncio</v-btn
        >
      </v-toolbar>

      <v-data-table
        class="elevation-1"
        :headers="headers"
        :items="tiposDeAnuncio"
        :search="search"
        @click:row="verAnuncios"
        :loading="cargandoLista || cargandoEliminacion"
        loading-text="Cargando tipos de anuncio"
      >
        <!--Botones de edicin y eliminacin en cada fila-->
        <template v-slot:[`item.edit`]="{ item }">
          <v-btn icon @click.stop.prevent="abrirDialogoEdicion(item)">
            <v-icon>create</v-icon>
          </v-btn>
        </template>

        <template v-slot:[`item.delete`]="{ item }">
          <v-btn icon @click.stop.prevent="abrirDialogoEliminacion(item.id)">
            <v-icon>delete</v-icon>
          </v-btn>
        </template>
      </v-data-table>

      <!--Formulario de edicin de tipo de anuncio en dilogo-->
      <v-dialog v-model="editDialog" max-width="700px">
        <v-card :loading="cargandoEdicion">
          <v-card-title>
            <span>Edicin de tipo de anuncio</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-form v-model="edicionValida" ref="form">
                <v-flex xs12 sm12 md12>
                  <v-text-field
                    v-model="tipoAnuncio.nombre"
                    label="Nombre"
                    :rules="reglaEditNombre"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 sm12 md12>
                  <v-textarea
                    v-model="tipoAnuncio.descripcion"
                    label="Descripcion"
                    :rules="reglaEditDescripcion"
                    rows="1"
                    auto-grow
                    row-height="12"
                  ></v-textarea>
                </v-flex>
              </v-form>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="blue-grey lighten-4" 
              large
              @click.native="cerrarDialogoEdicion"
              >Cancelar</v-btn
            >
            <v-btn
              color="blue-grey lighten-4" 
              large
              @click.native="abrirDialogoConfirmarEdicion"
              >Guardar</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>

      <!--Dilogo de confirmacin - Edicin de tipo de anuncio-->
      <dialogo-edicion
        :dialog="confirmEditionDialog"
        @close="confirmEditionDialog = false"
        titulo="tipo de anuncio"
        @editarValido="editar"
      >
      </dialogo-edicion>

      <!--Dilogo de confirmacin - Eliminacin de tipo de anuncio-->
      <dialogo-eliminacion
        :dialog="confirmEliminationDialog"
        titulo="tipo de anuncio"
        @close="confirmEliminationDialog = false"
        @borrarValido="eliminar"
      >
      </dialogo-eliminacion>
    </v-flex>
  </v-layout>
</template>

<script>
import DialogoEdicion from "../DialogosValidacion/DialogoEdicion.vue";
import DialogoEliminacion from "../DialogosValidacion/DialogoEliminacion.vue";
import axios from "axios";
export default {
  name: "TipoAnuncios",
  components: {
    DialogoEdicion,
    DialogoEliminacion,
  },
  data() {
    return {
      //Headers de la tabla
      headers: [
        { text: "Nombre", value: "nombre" },
        { text: "Descripcin", value: "descripcion" },
        { text: "Editar", value: "edit" },
        { text: "Eliminar", value: "delete" },
      ],

      //Variable para la bsqueda de tipos de anuncio por algn texto
      search:"",

      //Lista de tipos de anuncio
      tiposDeAnuncio: [],

      //Animaciones de cargando
      cargandoLista: false,
      cargandoEliminacion: false,
      cargandoEdicion: false,

      //--EDICIN--//
      
      editDialog: false, //Para abrir el formulario de edicin
      edicionValida: false, //Define si los campos son vlidos o no
      confirmEditionDialog: false, //Para abrir el dilogo de confirmar edicin

      //Modelo para la edicin de tipo de anuncio
      tipoAnuncio: {
        id: "",
        nombre: "",
        descripcion: "",
      },

      //Reglas para los campos de edicin
      reglaEditNombre: [
        (v) => !!v || "El nombre es requerido.",
        (v) => (v && v.length <= 50) || "Debe contener menos de 50 caracteres.",
        (v) =>
          /^[^\s]+(\s+[^\s]+)*$/.test(v) ||
          "No deben haber espacios al inicio o al final.",
      ],
      reglaEditDescripcion: [
        (v) => !!v || "La descripcin es requerida.",
        (v) =>
          (v && v.length <= 200 && v.length > 1) ||
          "Debe contener menos de 200 caracteres.",
        (v) =>
          /^[^\s]+(\s+[^\s]+)*$/.test(v) ||
          "No deben haber espacios al inicio o al final.",
      ],

      //--ELIMINACIN--//

      confirmEliminationDialog: false, //Para abrir dilogo de confirmar eliminacin
      id_delete: "", //ID del tipo de anuncio a eliminar

 
    };
  },

  created() {
    this.listar();
  },
  methods: {
    //Ir a pantalla de creacin
    crearNuevo() {
      this.$router.push("/tipo-anuncios/nuevo");
    },
    //Ir a anuncios del tipo seleccionado
    verAnuncios(item) {
      var tipo = item.nombre.toLowerCase().replace(/ /g, "_");
      //Para cambiar los datos del tipo de anuncio de Vuex al requerido (Ubicacin: store.js)
      this.$store.commit("changeValueOfAnnouncementType", {
        tipo_anuncio_id: item.id,
        nombre_tipo_anuncio: item.nombre,
      });
      this.$router.push("/tipo-anuncios/" + tipo);
    },
    //LISTADO
    listar() {
      this.cargandoLista = true;
      axios
        .get("tipo-anuncio")
        .then((response) => {
          //Ordenando alfabticamente
          this.tiposDeAnuncio = response.data.sort((a, b) => {
            return a.nombre
              .toLowerCase()
              .localeCompare(b.nombre.toLowerCase(), "es", {
                sensitivity: "base",
                numeric: true,
              });
          });
          this.cargandoLista = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //EDICIN
    abrirDialogoEdicion(item) {
      this.tipoAnuncio.id = item.id;
      this.tipoAnuncio.nombre = item.nombre;
      this.tipoAnuncio.descripcion = item.descripcion;
      this.editDialog = true;
    },
    cerrarDialogoEdicion() {
      this.editDialog = false;
    },
    abrirDialogoConfirmarEdicion() {
      //Muestra las validaciones del form si los campos no son vlidos
      this.$refs.form.validate();
      if (this.edicionValida) {
        this.confirmEditionDialog = true;
      }
    },
    editar() {
      this.confirmEditionDialog = false;
      this.cargandoEdicion = true;
      if (this.edicionValida) {
        axios
          .put(`tipo-anuncio/${this.tipoAnuncio.id}`, {
            nombre: this.tipoAnuncio.nombre,
            descripcion: this.tipoAnuncio.descripcion,
          })
          .then((response) => {
            this.cargandoEdicion = false;
            this.editDialog = false;
            this.listar();
            this.$alert("Edicin exitosa", "Exitoso", "success");
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },

    //ELIMINACIN
    abrirDialogoEliminacion(id) {
      this.id_delete = id;
      this.confirmEliminationDialog = true;
    },
    eliminar() {
      this.confirmEliminationDialog = false;
      this.cargandoEliminacion = true;
      axios
        .delete(`tipo-anuncio/${this.id_delete}`)
        .then((response) => {
          this.cargandoEliminacion = false;
          this.listar();
          this.$alert("Eliminacin exitosa", "Exitoso", "success");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

